<template>
  <div class="signin">
    <i class="sprites icon-logo-title"></i>
    <div class="sign-in">
      <p><i class="sprites icon-title"></i></p>
      <p><i class="sprites icon-email-text"></i></p>
      <p><i class="sprites icon-wave"></i></p>
      <div class="form top">
        <i class="sprites icon-user-icon"></i>
        <input type="text" placeholder="用户名"
               class="email-input"
               v-model="form.email">
        <div class="email-suffix">
          <div class="line"></div>
          <span>{{EmailSuffixCpt}}</span>
        </div>
      </div>
      <div class="form bottom">
        <i class="sprites icon-password-icon"></i>
        <input type="password" placeholder="密码"
               class="pas-input"
               v-model="form.password"></div>
      <p>
        <button class="submit" @click="signSubmit">登录</button>
      </p>
    </div>
  </div>
</template>
<script>
  import { mapState, mapActions } from 'vuex'
  import { EmailSuffix } from '../../utils/baseConfig'

  export default {
    name: '',
    props: {},
    data () {
      return {
        form: {
          email: '',
          password: ''
        }
      }
    },
    computed: {
      ...mapState([
        'loginStatus',
      ]),
      user () {
        return JSON.parse(sessionStorage.user)
      },
      EmailSuffixCpt () {
        return EmailSuffix
      }
    },
    watch: {
      loginStatus (s) {
        if (s.status) {
          this.$message({
            type: 'success',
            message: s.msg
          })
          this.$router.push({name: 'department'})
        } else if (s.msg) {
          this.$message({
            type: 'error',
            message: s.msg
          })
        }
      }
    },
    methods: {
      ...mapActions([
        'ac_login',
      ]),
      signSubmit () {
        if (this.form.email && this.form.password) {
          this.ac_login({
            email: this.form.email + EmailSuffix,
            password: this.form.password
          })
        } else {
          this.$message({
            type: 'warning',
            message: '用户名或密码不能为空'
          })
        }
      }
    },
    components: {},
    beforeCreate () {},
    created () {},
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../styles/sprites/sprites.css";

  .signin {
    background-image: url("../../assets/md_bg.jpg");
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;

    .icon-logo-title {
      margin-top: 20px;
      margin-left: 20px;
    }
  }

  .sign-in {
    text-align: center;
    margin-top: 160px;
    .icon-title {
      transform: scale(0.75);
    }
    .icon-email-text {
      transform: scale(0.9);
    }
    .form {
      border: 2px solid #fff;
      width: 270px;
      margin: 20px auto;
      border-radius: 20px;
      padding: 3px;

      .icon-user-icon {
        transform: scale(0.9);
      }
      .icon-password-icon {
        transform: scale(0.9);
      }
      input {
        border: 0;
        background-color: rgba(255, 255, 255, 0);
        position: relative;
        top: -3px;
        left: 3px;
        color: #fff;
        &:focus {
          outline: none
        }
        &::-webkit-input-placeholder {
          color: #FFF;
        }
        &:-moz-placeholder {
          color: #FFF;
        }
        &::-moz-placeholder {
          color: #FFF;
        }
        &:-ms-input-placeholder {
          color: #FFF;
        }
      }
      &.top {
        margin-top: 50px;
      }
      &.bottom {
        margin-bottom: 50px;
      }
    }
    .submit {
      background-color: #FED100;
      width: 180px;
      height: 40px;
      border-radius: 20px;
      border: 0;
    }
  }

  .email-suffix {
    display: inline-block;
    position: relative;
    top: -3px;
    left: 10px;
    color: #fff;
    .line {
      display: inline-block;
      position: absolute;
      height: 18px;
      width: 2px;
      top: 2px;
      background-color: #fff;
    }
    span {
      padding-left: 8px;
    }
  }

  .email-input {
    width: 100px;
  }

  .pas-input {
    width: 198px;
  }
</style>



